.elements {
  padding: 50px 0 0 0;
  margin: 0 auto;
  max-width: 880px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
  column-gap: 17px;
  row-gap: 20px;
}
  
@media screen and (max-width: 880px) {
  .elements {
    width: 88%;
    width: calc(100% - 2*50px);
  }
}
  
@media screen and (max-width: 680px) {
  .elements {
    padding: 36px 0 0 0;
    width: calc(100% - 2*19px);
  }
}
  

 